import React from 'react';
import ShowCovers from './ShowCovers'
import stylesheet from '../css/CoverList.css'

class CoverList extends React.Component{
    // constructor(props){
    //     super(props);
    // }


    onSelectReturn = (selected) => {
        this.props.onSelectReturn([selected, false]);
    }

    backToGallery = () => {
        this.props.onSelectReturn([[], true]);
    }

    render(){
        if(this.props.showGallery)
            return(
                <div id="coverListBody" >
                    <ShowCovers images={this.props.images} onSelectReturn={this.onSelectReturn} isMobile={this.props.isMobile} />
                </div>
            )
        else
            return(
                <div id="coverBigBody" className='flex' >
                    <img src={this.props.selectedImage[1]} alt={"sem tags por agora"} />
                    <i className='big close icon' onClick={this.backToGallery}></i>
                </div>
            )
    }
    
}

export default CoverList;