import React from "react";
import BooksBar from './BooksBar';


class BookList extends React.Component{
    // constructor(props){
    //     super(props);
    // }

    render(){
        if(this.props.books.length === 0)
            return(
                <div>
                    <div className="ui relaxed divided list">
                        <div className="item">
                            <i className="large window close outline icon"></i>
                            <div className="content">
                                {/* <a className="header"></a> */}
                                <div className="description">
                                    Nenhum livro adicionado até o momento.
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            )
        else
            return(
                <BooksBar books={this.props.books} selectBook={this.props.selectBook}/>
            )
    }

}




export default BookList;