import React from "react";
import BookAdd from './BookAdd'
import SearchCover from './SearchCover';
import stylesheet  from '../css/BookForm.css'


class BookForm extends React.Component{
    // constructor(props){
    //     super(props);
    // }

    render(){
        return(
            <div id='mainBody'>
                <SearchCover onSearchSubmit={this.props.onCoverSearchSubmit}/>
                <div id='formBody' className='ui placeholder segment'>
                    <BookAdd images={this.props.images} showGallery={this.props.showGallery} showingGallery={this.props.showingGallery} onSelectInfoReturn={this.props.onSelectInfoReturn} isMobile={this.props.isMobile} />
                </div>
            </div>
        )
    }

}

export default BookForm;