import React from 'react';
import stylesheet from '../css/SearchCover.css';


class SearchCover extends React.Component{
    // constructor(props){
    //     super(props);
    // }

    state = { entry: '' }

    onFormSubmit = (event) => {
        event.preventDefault()
        this.props.onSearchSubmit([this.state.entry, true]);
    }

    render() {
        return(
            <form action="ui form" onSubmit={this.onFormSubmit}>
                <div className='ui icon input'>
                    <input 
                        type="text" 
                        placeholder='Buscar...'
                        onChange={(event) => this.setState({entry:event.target.value})}
                        value={this.state.entry}
                    ></input>
                    <i className='search icon'></i>
                </div>
            </form>
        )
    }
}

export default SearchCover;