import React from 'react';
import ReactHeight from 'react-height';

class ShowCovers extends React.Component {
    constructor(props){
        super();
    }

    state = {
        listHeight: 0
    }

    calcHeight = (height) =>{
        let value;
        if(this.props.isMobile)
            value = height * .45;
        else
            value = height * .6;
        // console.log(value);
        var state = {
            maxWidth: "33%",
            maxHeight: String(value) + 'px'
        }
        this.setState({listHeight: state});
    }


    render(){
        
        return(
            <ReactHeight onHeightReady={(height) => this.calcHeight(height)} style={{width: "100%", height: "100%"}}>
                <div id='coverList' className="ui horizontal selection list" >
                    {this.props.images.map((image, key) =>{
                        return  <div key={key} className="item" style={{maxWidth: this.state.listHeight.maxWidth, maxHeight: this.state.listHeight.maxHeight}}>
                                    <img src={image[0]} alt={"testando ainda"}  style={{maxWidth: "100%", height: this.state.listHeight.maxHeight}} onClick={(event) => this.props.onSelectReturn(image)}/>
                                </div>
                    })}
                </div>
            </ReactHeight>
        )
    }
    
}

export default ShowCovers;