import React from 'react';
import axios from 'axios';
import Modal from "react-modal";
// import { BrowserRouter, Route } from 'react-router-dom';
import Menu from './Menu';
import BookForm from './BookForm';
import ScreenSize from './ScreenSize';
import stylesheet from '../css/App.css';

class App extends React.Component{

    constructor(props) {
        super();
        const booksStorage = localStorage.getItem('books');
        // console.log(JSON.parse(booksStorage));
        this.state = {
                images: [],
                books: (booksStorage != null)? JSON.parse(booksStorage) : Array(0),
                showCoverGallery: true,
                selectedFile: [],
                modalSure: false,
                modalAdd: false,
                modalUpload: false
        }
    }

    downloadTxtFile = () => {
        const element = document.createElement("a");
        const file = new Blob([JSON.stringify(this.state.books)], {
            type: "text/plain"
        });
        element.href = URL.createObjectURL(file);
        element.download = "booksData.txt";
        document.body.appendChild(element);
        element.click();
    }

    uploadTxtFile = async () => {
        // let data = await fetch(booksData).then(function(res){
        //     return res.text();
        // }).then(function(data){
        //     return data;
        // })

        // this.setState({books: JSON.parse(data)});
    }
    // uploadTxtFile = async () => {
        // let data = await fetch(booksData).then(function(res){
        //     return res.text();
        // }).then(function(data){
        //     return data;
        // })

    //     this.setState({books: JSON.parse(data), booksLoaded: true});
    // }
    // if(!this.state.booksLoaded){
    //     this.uploadTxtFile();
    // }

    selectImages = (hits) => {
        let images =[];
        for(let i = 0; i<hits.length; i++){
            // console.log(hits[i]);
            if(hits[i].volumeInfo.imageLinks)
                images.push([hits[i].volumeInfo.imageLinks.smallThumbnail, hits[i].volumeInfo.imageLinks.thumbnail, hits[i].id, hits[i].volumeInfo.title, hits[i].volumeInfo.authors[0], hits[i].volumeInfo.description]);
        }
        return images;
    }

    onCoverSearchSubmit = async (entry) =>{
    //    const response = await axios.get(`https://pixabay.com/api/?key=26575564-686dca77c9e10fd6d99198209&q=${entry[0]}&image_type=photo`)
        const response = await axios.get(`https://www.googleapis.com/books/v1/volumes?q=${entry[0]}&key=AIzaSyCERShy1R9acWRkkCVtpi09uSNPXAdujgs`)
        // console.dir(response.data.items);
        let images = this.selectImages(response.data.items);
        // console.log(images);
        this.setState({images: images, showCoverGallery: entry[1]})
    }

    onSelectInfoReturn = (entry) =>{
        if((entry[0].length === 0) || (entry[1].length === 0) || (entry[3].length === 0)){
            console.log("invalid info!!");
        }else{
            if(this.state.books.lenth === 0)
                this.setState({books: {entry}});
            else
                this.setState(prevState => ({
                    books: [...prevState.books, {entry}]
                }))

            this.setState({showCoverGallery: true});
            this.changeModalStateAdd();
        }
    }

    showingGallery = (entry) => {
        this.setState({showCoverGallery: entry})
    }

    addQuote = (entry, id) => {
        let bookList = this.state.books;
        for(let i = 0; i<this.state.books.length; i++)
            if(bookList[i].entry[6] === id){
                if(bookList[i].entry[7] === 0)
                    bookList[i].entry[4] = [entry];
                else{
                    bookList[i].entry[4].push(entry);
                }
                bookList[i].entry[7]++;  
            }

        this.setState({books: bookList});
    }

    remQuote = (id) => {
        let bookList = this.state.books;
        for(let i = 0; i<bookList.length; i++)
            if(bookList[i].entry[6] === id[0])
                for(let j = 0; j<bookList[i].entry[7]; j++)
                    if(bookList[i].entry[4][j][6] === id[1]){
                        bookList[i].entry[4].splice(j, 1);
                        bookList[i].entry[7] -= 1;
                    }

        this.setState({books: bookList});
    }

    editQuote = (quote, id) => {
        let bookList = this.state.books;
        for(let i = 0; i<bookList.length; i++)
            if(bookList[i].entry[6] === id)
                for(let j = 0; j<bookList[i].entry[7]; j++)
                    if(bookList[i].entry[4][j][6] === quote[6]){
                        if(quote[5].includes(",")){
                            console.log(quote[5]);
                            let tag = [];
                            tag = quote[5].split(",");
                            if(tag[tag.length-1] === "")
                                tag.splice(tag.length-1, 1);
                            console.log(tag);
                            quote[5] = tag;
                        }   
                        
                        bookList[i].entry[4][j] = quote;
                    }
                        
    }

    remBook = (id) => {
        let bookList = this.state.books;
        for(let i = 0; i<bookList.length; i++)
            if(bookList[i].entry[6] === id)
                bookList.splice(i, 1);

        this.setState({books: bookList});
    }

    editBook = (book) => {
        // console.log(book);
        // console.log(this.state.books[0].entry)
        let bookList = this.state.books;
        for(let i = 0; i<bookList.length; i++)
            if(bookList[i].entry[6] === book[6])
                bookList[i].entry = book;

        this.setState({books: bookList});
    }


    saveOnStorage = () => {
        localStorage.setItem('books', JSON.stringify(this.state.books));

        this.changeModalState();
    }

    changeModalState = () => {
        this.setState({modalSure: !this.state.modalSure});
    }

    changeModalStateAdd = () => {
        this.setState({modalAdd: !this.state.modalAdd, images: []});
    }

    changeModalStateUpload = () => {
        this.setState({modalUpload: !this.state.modalUpload});
    }

    render(){
        return(
            <div>
                {/* <ScreenSize getWindowSize={this.getWindowSize} /> */}
                <Menu 
                    onCoverSearchSubmit={this.onCoverSearchSubmit} 
                    images={this.state.images}
                    showGallery={this.state.showCoverGallery}
                    showingGallery={this.showingGallery}
                    onSelectInfoReturn={this.onSelectInfoReturn}
                    books={this.state.books}
                    windowsChange={this.changeModalStateAdd}
                    addQuote={this.addQuote}
                    remQuote={this.remQuote}
                    editQuote={this.editQuote}
                    remBook={this.remBook}
                    editBook={this.editBook}
                    saveOnStorage={this.changeModalState}
                    downloadTxt={this.downloadTxtFile}
                    uploadTxt={this.changeModalStateUpload}
                    isMobile={this.props.isMobile}
                    minimalDescription={this.props.minimalDescription}
                />
                <Modal id="areUsureModal" isOpen={this.state.modalSure} onRequestClose={this.changeModalState} style={{
                    overlay: {
                        color: "lightgrey"
                    },
                    content: {
                        color: 'black',
                        height: "20%",
                        top: "25%",
                        width: "30%",
                        left: "35%"
                    }}}
                >
                    <div id="sureModalDiv" style={{}}>
                        <p>Tem certeza que gostaria de salvar? (Os livros previamente gravados em memória serão perdidos)</p>
                        <div>
                            <button className="ui primary button" onClick={this.saveOnStorage}>Salvar</button>    <button className="ui button" onClick={this.changeModalState}>Cancelar</button>
                        </div>
                    </div>
                </Modal>
                <Modal id="uploadModal" isOpen={this.state.modalUpload} onRequestClose={this.changeModalStateUpload} style={{
                    overlay: {
                        color: "lightgrey"
                    },
                    content: {
                        color: 'black',
                        height: "20%",
                        top: "25%",
                        width: this.props.isMobile == true ? "80%" : "30%",
                        left: this.props.isMobile == true ? "10%" : "35%"
                    }}}
                >
                    <div id="" >
                        <input type="file" onChange={(event) => {this.setState({selectedFile: event.target})}}/>
                        {/* <div>
                            <button className="ui primary button" onClick={this.saveOnStorage}>Salvar</button>    <button className="ui button" onClick={this.changeModalState}>Cancelar</button>
                        </div> */}
                    </div>
                </Modal>
                <Modal id="addModal" isOpen={this.state.modalAdd} onRequestClose={this.changeModalStateAdd} style={{
                    overlay: {
                        color: "lightgrey"
                    },
                    content: {
                        color: 'black',
                        backgroundColor: "transparent",
                        border: "0px",
                        height: "80%",
                        top: "10%",
                        width: "100%",
                        left: "0"
                    }}}
                >
                    <BookForm images={this.state.images} showGallery={this.state.showCoverGallery} showingGallery={this.showingGallery} onSelectInfoReturn={this.onSelectInfoReturn} onCoverSearchSubmit={this.onCoverSearchSubmit} back={this.windowsChange} isMobile={this.props.isMobile} />
                </Modal>
            </div>
        )
    }
    
}

export default App;