import React from "react";
import stylesheet from '../css/BooksBar.css'


class BooksBar extends React.Component{
    // constructor(props){
    //     super(props);
    // }

    render(){
        // console.log(this.props.books[0])
        const list = this.props.books.map((book, i) => <div id="bookItem" className="item" key={i} onClick={(event) => this.props.selectBook(book)}>
                                                            <div style={{display: "flex", flexDiretion: "flex-column"}}>
                                                                <img src={book.entry[3][0]} style={{width: "100%"}} alt="book cover" />
                                                                
                                                            </div>
                                                            <div className="description">
                                                                <div id="nCitations">
                                                                    {book.entry[7]}
                                                                </div>
                                                                <div><i>{book.entry[0]}</i></div>
                                                                <div>{book.entry[1]}</div>
                                                                
                                                            </div>
                                                        </div>);
        return(
            <div className="ui relaxed divided selection list">
                {list}
            </div>
        )
    }
}


export default BooksBar;