import React from "react";
import Modal from "react-modal";
import CopyToClipboard from "react-copy-to-clipboard";
import {TextArea} from "semantic-ui-react"
import stylesheet from "../css/QuoteList.css"

class QuoteList extends React.Component{


    state = {
        modalAdd: false,
        modalZoom: false,
        modalEdit: false,
        modalSureHandler: false,
        quoteChapterNr: [],
        quoteChapterName: [],
        quotePag: [],
        quoteParagraph: [],
        quoteText: [],
        quoteTags: [],
        selectedQuote: []
    }

    changeModalStateAdd = () => {
        if(this.props.isMobile){
            this.props.changeHideHandler();
            this.props.changeMenuHandler();
        }
        this.setState({modalAdd: !this.state.modalAdd})
    }

    changeModalStateZoom = (quote) => {
        if(this.props.isMobile){
            this.props.changeHideHandler();
            this.props.changeMenuHandler();
        }
        if(quote)
            this.setState({modalZoom: !this.state.modalZoom, selectedQuote: quote})
        else
            this.setState({modalZoom: !this.state.modalZoom, selectedQuote: []})
    }

    changeModalStateEdit = () => {
        if(!this.state.modalEdit){
            this.setState({quoteChapterName: this.state.selectedQuote[1]});
            this.setState({quoteChapterNr: this.state.selectedQuote[0]});
            this.setState({quotePag: this.state.selectedQuote[2]});
            this.setState({quoteParagraph: this.state.selectedQuote[3]});
            this.setState({quoteText: this.state.selectedQuote[4]});
            this.setState({quoteTags: this.state.selectedQuote[5]});

            this.setState({modalZoom: !this.state.modalZoom})
        }else{
            this.setState({quoteChapterName: []});
            this.setState({quoteChapterNr: []});
            this.setState({quotePag: []});
            this.setState({quoteParagraph: []});
            this.setState({quoteText: []});
            this.setState({quoteTags: []});

            if(this.props.isMobile){
                this.props.changeHideHandler();
                this.props.changeMenuHandler();
            }
        }

        this.setState({modalEdit: !this.state.modalEdit})
    }

    changeModalSureState = () => {
        this.setState({modalSureHandler: !this.state.modalSureHandler});
    }

    returnDataEdit = () => {
        this.props.editQuote([this.state.quoteChapterNr, this.state.quoteChapterName, this.state.quotePag, this.state.quoteParagraph, this.state.quoteText, this.state.quoteTags, this.state.selectedQuote[6]], this.props.bookInfo.entry[6]);

        this.changeModalStateEdit();
    }

    returnData = () => {
        if((this.state.quoteChapterNr === [] || this.state.quoteChapterNr === "") && (this.state.quoteChapterName === [] || this.state.quoteChapterName === "") && (this.state.quotePag === [] || this.state.quotePag === "") && (this.state.quoteParagraph === [] || this.state.quoteParagraph === "") && (this.state.quoteText === [] || this.state.quoteText === ""))
            console.log("Erro!");
        else{
            this.props.addQuote([this.state.quoteChapterNr, this.state.quoteChapterName, this.state.quotePag, this.state.quoteParagraph, this.state.quoteText, this.props.bookInfo.entry[2], this.props.bookInfo.entry[7]], this.props.bookInfo.entry[6]);
            this.changeModalStateAdd();
        }
        
    }

    returnForRemoval = () => {
        this.props.remQuote([this.props.bookInfo.entry[6], this.state.selectedQuote[6]]);

        this.changeModalSureState();
        this.changeModalStateZoom();
    }

    render(){
        const list = this.props.quotes.map((quote, i) => <div id="quoteItem" className="item" key={i} onClick={(event) => {this.changeModalStateZoom(quote)}}>
                                                            <span style={{float: "right"}}>Pag. {quote[2]}</span>
                                                            <span style={{float: "left", marginRight:"10px"}}>{i+1}</span>
                                                            <span id="chapterName">  Capítulo {quote[0]}</span> <span id="chapterNr">(ou <i>{quote[1]})</i></span>
                                                            {quote[4].length > 60 ? <p id="quoteText">{quote[4].substr(0, 60)}...</p> : <p id="quoteText">{quote[4]}</p>}
                                                        </div>);
        return(
            <div style={{height: "100%"}}>
                <div>
                    <h2>Citações <i className="plus circle icon" onClick={this.changeModalStateAdd} style={{cursor: 'pointer'}}></i></h2>
                </div>
                <Modal id="quoteAddModal" isOpen={this.state.modalAdd} onRequestClose={this.changeModalStateAdd} contentLabel="Adicionar uma Citação" style={{
                    overlay: {
                        color: "lightgrey"
                    },
                    content: {
                        color: 'black',
                        height: "70%",
                        top: "15%",
                        width: this.props.isMobile == true ? "80%" : "60%",
                        left: this.props.isMobile == true ? "10%" : "20%"
                    }}}
                >
                    {
                        this.props.isMobile == true ?
                            <div>
                                <h4 className="ui horizontal divider header">
                                    Adicionar Citação
                                </h4>
                                <form className="ui form" style={{marginTop: "5%"}}>
                                    <div className="ui mini labeled input">
                                        <div className="ui label">
                                            Capítulo:
                                        </div>
                                        <input type="text" style={{width: "200px"}} onChange={(event) => this.setState({quoteChapterName: event.target.value})}/>
                                    </div>
                                </form>
                                <form className="ui form" style={{marginTop: "5%"}}>
                                    <div className="ui mini labeled input" >
                                        <div className="ui label">
                                            Número: 
                                        </div>
                                        <input type="text" maxLength={3} style={{width: "60px"}} pattern={[0-9]} onChange={(event) => this.setState({quoteChapterNr: event.target.value})}/>
                                    </div>
                                </form>
                                <form className="ui form" style={{marginTop: "5%"}}>
                                    <div className="ui mini labeled input">
                                        <div className="ui label">
                                            Página:
                                        </div>
                                        <input type="text" maxLength={4} style={{width: "60px"}} pattern={[0-9]} onChange={(event) => this.setState({quotePag: event.target.value})}/>
                                    </div>
                                    <div className="ui mini labeled input" style={{marginLeft: "20px"}}>
                                        <div className="ui label">
                                            Parágrafo: 
                                        </div>
                                        <input type="text" maxLength={2} style={{width: "50px"}} pattern={[0-9]} onChange={(event) => this.setState({quoteParagraph: event.target.value})}/>
                                    </div>
                                </form>
                                <form className="ui form" style={{marginTop: "5%"}}>
                                    <div className="ui label">
                                        Descrição:
                                    </div>
                                    <TextArea onChange={(event) => this.setState({quoteText: event.target.value})}/>
                                </form>
                                <div style={{position: "absolute", right: "5%", bottom: "5%"}}>
                                    <button className="ui primary button" onClick={this.returnData}>Salvar</button>    <button className="ui button" onClick={this.changeModalStateAdd}>Cancelar</button>
                                </div>    
                            </div>
                            :
                            <div>
                                <h4 className="ui horizontal divider header">
                                    Adicionar Citação
                                </h4>
                                <form className="ui form">
                                    <div className="ui mini labeled input">
                                        <div className="ui label">
                                            Capítulo:
                                        </div>
                                        <input type="text" style={{width: "200px"}} onChange={(event) => this.setState({quoteChapterName: event.target.value})}/>
                                    </div>
                                    <div className="ui mini labeled input" style={{marginLeft: "20px"}}>
                                        <div className="ui label">
                                            Número: 
                                        </div>
                                        <input type="text" maxLength={3} style={{width: "60px"}} pattern={[0-9]} onChange={(event) => this.setState({quoteChapterNr: event.target.value})}/>
                                    </div>
                                </form>
                                <form className="ui form" style={{marginTop: "5%"}}>
                                    <div className="ui mini labeled input">
                                        <div className="ui label">
                                            Página:
                                        </div>
                                        <input type="text" maxLength={4} style={{width: "60px"}} pattern={[0-9]} onChange={(event) => this.setState({quotePag: event.target.value})}/>
                                    </div>
                                    <div className="ui mini labeled input" style={{marginLeft: "20px"}}>
                                        <div className="ui label">
                                            Parágrafo: 
                                        </div>
                                        <input type="text" maxLength={2} style={{width: "50px"}} pattern={[0-9]} onChange={(event) => this.setState({quoteParagraph: event.target.value})}/>
                                    </div>
                                </form>
                                <form className="ui form" style={{marginTop: "5%"}}>
                                    <TextArea onChange={(event) => this.setState({quoteText: event.target.value})}/>
                                </form>
                                <div style={{position: "absolute", right: "5%", bottom: "5%"}}>
                                    <button className="ui primary button" onClick={this.returnData}>Salvar</button>    <button className="ui button" onClick={this.changeModalStateAdd}>Cancelar</button>
                                </div>  
                            </div>
                    }
                                      
                </Modal>
                <Modal id="quoteZoomModal" isOpen={this.state.modalZoom} onRequestClose={this.changeModalStateZoom} style={{
                    overlay: {
                        color: "lightgrey"
                    },
                    content: {
                        color: 'black',
                        height: "60%",
                        top: "20%",
                        width: this.props.isMobile == true ? "80%" : "",
                        left: this.props.isMobile == true ? "10%" : "35%",
                        right: this.props.isMobile == true ? "10%%" : "35%",
                    }}}
                >
                    <div style={{position:"absolute", right:"0", top: "2px"}}>
                        <button className="ui button" onClick={this.changeModalStateZoom}>Fechar</button>
                    </div>
                    <div className='ui icon button' style={{position: "absolute", bottom: "2%", right: "2%", backgroundColor: "transparent"}} onClick={this.changeModalSureState}>
                        <i className="trash icon big"></i>
                    </div>
                    <CopyToClipboard style={{position: "absolute", bottom: "17%", right: "2%", backgroundColor: "transparent"}} text={"\"" + String(this.state.selectedQuote[4] + "\" - pag. " + this.state.selectedQuote[2] + ", par. " + this.state.selectedQuote[3] + " (" + this.props.bookInfo.entry[0] + ", " + this.props.bookInfo.entry[1] + ")")} onCopy={(text) => console.log(text)}>
                        <div className='ui icon button' style={{backgroundColor: "transparent"}} >
                            <i className="copy icon big"></i>
                        </div>
                    </CopyToClipboard>
                    <div className='ui icon button' style={{position: "absolute", bottom: "32%", right: "2%", backgroundColor: "transparent"}} onClick={this.changeModalStateEdit}>
                        <i className="edit icon big"></i>
                    </div>
                    
                    
                    <div style={{height: "100%", width: this.props.isMobile == true ? "70%" : "80%", display: "flex", flexDirection:"column"}}>
                            <h4 className="ui horizontal divider header">
                                Capítulo <b>{this.state.selectedQuote[0]}</b>
                            </h4>
                            {/* <h3>{this.state.selectedQuote[1]}</h3> */}

                            <p style={{marginBottom:"5px"}}>"{this.state.selectedQuote[4]}" - pag. {this.state.selectedQuote[2]}, par. {this.state.selectedQuote[3]} </p>
                            {/* <p>Tags: {this.props.quotes.map((quote, i) => {quote.}
                                }</p> */}
                    </div>
                </Modal>
                <Modal id="quoteEditModal" isOpen={this.state.modalEdit} onRequestClose={this.changeModalStateEdit} style={{
                    overlay: {
                        color: "lightgrey"
                    },
                    content: {
                        color: 'black',
                        height: "60%",
                        top: "20%",
                        width: this.props.isMobile == true ? "80%" : "50%",
                        left: this.props.isMobile == true ? "10%" : "25%"
                    }}}
                >
                    {
                        this.props.isMobile == true ?
                            <div>
                                <h4 className="ui horizontal divider header">
                                    Editar Citação <b>{this.state.selectedQuote[6]+1}</b>
                                </h4>
                                <form className="ui form" style={{marginTop: "5%"}}>
                                    <div className="ui small labeled input">
                                        <div className="ui label">
                                            Capítulo:
                                        </div>
                                        <input type="text" style={{width: "200px"}} value={this.state.quoteChapterName} onChange={(event) => this.setState({quoteChapterName: event.target.value})}/>
                                    </div>
                                </form>
                                <form className="ui form" style={{marginTop: "5%"}}>
                                    <div className="ui small labeled input">
                                        <div className="ui label">
                                            Número: 
                                        </div>
                                        <input type="text" maxLength={3} style={{width: "60px"}} pattern={[0-9]} value={this.state.quoteChapterNr} onChange={(event) => this.setState({quoteChapterNr: event.target.value})}/>
                                    </div>
                                </form>
                                <form className="ui form" style={{marginTop: "5%"}}>
                                    <div className="ui mini labeled input">
                                        <div className="ui label">
                                            Página:
                                        </div>
                                        <input type="text" maxLength={4} style={{width: "60px"}} pattern={[0-9]} value={this.state.quotePag} onChange={(event) => this.setState({quotePag: event.target.value})}/>
                                    </div>
                                    <div className="ui mini labeled input" style={{marginLeft: "20px"}}>
                                        <div className="ui label">
                                            Parágrafo: 
                                        </div>
                                        <input type="text" maxLength={2} style={{width: "50px"}} pattern={[0-9]} value={this.state.quoteParagraph} onChange={(event) => this.setState({quoteParagraph: event.target.value})}/>
                                    </div>
                                </form>
                                <form className="ui form" style={{marginTop: "5%"}}>
                                    <div className="ui small fluid labeled input">
                                        <div className="ui label">
                                            Tags:
                                        </div>
                                        <input type="text" value={this.state.quoteTags} onChange={(event) => this.setState({quoteTags: event.target.value})}/>
                                    </div>
                                </form>
                                <form className="ui form" style={{marginTop: "5%"}}>
                                    <TextArea value={this.state.quoteText} onChange={(event) => this.setState({quoteText: event.target.value})}/>
                                </form>
                                <div style={{position: "absolute", right: "5%", bottom: "5%"}}>
                                    <button className="ui primary button" onClick={this.returnDataEdit}>Salvar</button>    <button className="ui button" onClick={this.changeModalStateEdit}>Cancelar</button>
                                </div>
                            </div>
                            :
                            <div>
                                <h4 className="ui horizontal divider header">
                                    Editar Citação <b>{this.state.selectedQuote[6]+1}</b>
                                </h4>
                                <form className="ui form">
                                    <div className="ui small labeled input">
                                        <div className="ui label">
                                            Capítulo:
                                        </div>
                                        <input type="text" style={{width: "200px"}} value={this.state.quoteChapterName} onChange={(event) => this.setState({quoteChapterName: event.target.value})}/>
                                    </div>
                                    <div className="ui small labeled input" style={{marginLeft: "20px"}}>
                                        <div className="ui label">
                                            Número: 
                                        </div>
                                        <input type="text" maxLength={3} style={{width: "60px"}} pattern={[0-9]} value={this.state.quoteChapterNr} onChange={(event) => this.setState({quoteChapterNr: event.target.value})}/>
                                    </div>
                                </form>
                                <form className="ui form" style={{marginTop: "5%"}}>
                                    <div className="ui small labeled input">
                                        <div className="ui label">
                                            Página:
                                        </div>
                                        <input type="text" maxLength={4} style={{width: "60px"}} pattern={[0-9]} value={this.state.quotePag} onChange={(event) => this.setState({quotePag: event.target.value})}/>
                                    </div>
                                    <div className="ui small labeled input" style={{marginLeft: "20px"}}>
                                        <div className="ui label">
                                            Parágrafo: 
                                        </div>
                                        <input type="text" maxLength={2} style={{width: "50px"}} pattern={[0-9]} value={this.state.quoteParagraph} onChange={(event) => this.setState({quoteParagraph: event.target.value})}/>
                                    </div>
                                </form>
                                <form className="ui form" style={{marginTop: "5%"}}>
                                    <div className="ui small fluid labeled input">
                                        <div className="ui label">
                                            Tags:
                                        </div>
                                        <input type="text" value={this.state.quoteTags} onChange={(event) => this.setState({quoteTags: event.target.value})}/>
                                    </div>
                                </form>
                                <form className="ui form" style={{marginTop: "5%"}}>
                                    <TextArea value={this.state.quoteText} onChange={(event) => this.setState({quoteText: event.target.value})}/>
                                </form>
                                <div style={{position: "absolute", right: "5%", bottom: "5%"}}>
                                    <button className="ui primary button" onClick={this.returnDataEdit}>Salvar</button>    <button className="ui button" onClick={this.changeModalStateEdit}>Cancelar</button>
                                </div>
                            </div>
                    }
                </Modal>
                <Modal id="areUsureModal" isOpen={this.state.modalSureHandler} onRequestClose={this.changeModalSureState} style={{
                        overlay: {
                            color: "lightgrey"
                        },
                        content: {
                            color: 'black',
                            height: "20%",
                            top: "25%",
                            width: this.props.isMobile == true ? "80%" : "30%",
                            left: this.props.isMobile == true ? "10%" : "35%"
                        }}}
                    >
                        <div style={{height: "100%", display: "flex", flexDirection: "column",justifyContent: "center"}}>
                            <p>Tem certeza que gostaria de deletar a citação?</p>
                            <div>
                                <button className="ui primary button" onClick={this.returnForRemoval}>Deletar</button>    <button className="ui button" onClick={this.changeModalSureState}>Cancelar</button>
                            </div>
                        </div>
                    </Modal>
                <div className="ui relaxed selection divided list" style={{height: "100%", overflowY: "scroll"}}>
                    {list}
                </div>
            </div>
        )

    }

}

export default QuoteList;