import React, { useState, useEffect } from "react";
import ReactDOM from "react";

import App from "./App";

const LandingPage = (props) => {
    
    const [isMobile, setIsMobile] = useState(false);
    const [minimalDescription, setMinDescription] = useState(false);

    useEffect(() => {
      window.screen.width <= 1000 ? setIsMobile(true) : setIsMobile(false);
      window.screen.width <= 500 ? setMinDescription(true) : setMinDescription(false);
    }, [window.screen.width]);

    function detectWindowSize() {
        window.innerWidth <= 1000 ? setIsMobile(true) : setIsMobile(false);   
        window.innerWidth <= 500 ? setMinDescription(true) : setMinDescription(false);     
    }
    
    window.onresize = detectWindowSize;

    // console.log(isMobile);
    // console.log(minimalDescription);

     return(
         <div>
            <App isMobile={isMobile} minimalDescription={minimalDescription} />
         </div>
     )
}

export default LandingPage;