import React from 'react';
import Modal from 'react-modal/lib/components/Modal';
import BookList from './BookList';
import BookDescription from "./BookDescription"
import stylesheet from '../css/Menu.css'

class Menu extends React.Component{
    // constructor(props){
    //     super(props);

    // }

    state = {
        book: [],
        modalBar: false,
        modalHide: false,
        hideButtonHandler: false,
        menuButtonHandler: true
    }

    changeModalStateBar = () => {
        this.changeMenuHandler();
        this.setState({modalBar: !this.state.modalBar, modalHide: !this.state.modalHide});
    }

    changeMenuHandler = (bool) => {
        if(bool)
            this.setState({menuButtonHandler: bool});
        else
            this.setState({menuButtonHandler: !this.state.menuButtonHandler});
    }

    changeHideHandler = (bool) => {
        if(bool)
            this.setState({hideButtonHandler: bool});
        else
            this.setState({hideButtonHandler: !this.state.hideButtonHandler});
    }

    returnOnClick = (book) => {
        this.changeModalStateBar();
        this.changeHideHandler();
        this.setState({book: book});
    }

    returnForUpload = () => {
        this.changeModalStateBar();
        this.props.uploadTxt();
    }

    returnForAdd = () => {
        this.changeModalStateBar();
        this.props.windowsChange();
    }

    whenDeleting = (id) => {
        if(this.state.book.entry[6] === id){
            this.props.remBook(id);
            this.setState({book: []});
        }
    }

    closeBook = () => {
        this.changeHideHandler();
        this.setState({book: []});
    }

    menuButton = () => {
        if(this.props.isMobile & this.state.menuButtonHandler)
            return(
                <div className='ui icon button' >
                    <i class="bars icon" onClick={this.changeModalStateBar}></i>
                </div>
            );
    }

    hideButton = () => {
        if(this.state.hideButtonHandler)
            return(
                <div className='ui icon button' >
                    <i className="eye slash icon" onClick={this.closeBook}></i>
                </div>
            );
            
    }


    render(){
        if(!this.props.isMobile)
            return(
                <div id='menuBody' className=''>
                    <div id='menu' className='ui segment'>
                        <div id='menuBar' className='ui segment'>
                            <div id='barIcons'>
                                <div className='ui icon button big' data-content="Add a book" onClick={this.props.windowsChange}>
                                    <i className="plus icon large"></i>
                                </div>
                                <div className='ui icon button big' onClick={this.props.downloadTxt}>
                                    <i className="download icon large"></i>
                                </div>
                                <div className='ui icon button big' onClick={this.props.uploadTxt}>
                                    <i className="upload icon large"></i>
                                </div>
                                <div className='ui icon button big' onClick={this.props.saveOnStorage}>
                                    <i className="save icon large"></i>
                                </div>
                            </div>
                            <div id='barBooks' className='ui segment'>
                                <BookList books={this.props.books} selectBook={this.returnOnClick} />
                            </div>
                        </div>
                        <div id='menuDivider' >
                            <div className='ui icon button' data-content="Add a book" >
                                <i className="eye slash icon" onClick={this.closeBook}></i>
                            </div>
                        </div>
                        <div id='content' className='ui segment'>
                            <BookDescription 
                                onCoverSearchSubmit={this.props.onCoverSearchSubmit}
                                images={this.props.images}
                                showGallery={this.props.showGallery}
                                showingGallery={this.props.showingGallery}
                                onSelectInfoReturn={this.props.onSelectInfoReturn}
                                selectedBook={this.state.book}
                                addQuote={this.props.addQuote}
                                remQuote={this.props.remQuote}
                                editQuote={this.props.editQuote}
                                remBook={this.whenDeleting}
                                editBook={this.props.editBook}
                                isMobile={this.props.isMobile}
                                minimalDescription={this.props.minimalDescription}
                                changeHideHandler={this.changeHideHandler}
                                changeMenuHandler={this.changeMenuHandler}
                            />
                        </div>
                    </div>
                </div>
            )
        else
            return(
                <div id='menuBody' className=''>
                    <div id='menu' className='ui segment'>
                        <div id='menuDividerMobile' >
                            {this.menuButton()}
                            {this.hideButton()}
                        </div>
                        <div id='contentMobile' className='ui segment'>
                            <BookDescription
                                onCoverSearchSubmit={this.props.onCoverSearchSubmit}
                                images={this.props.images}
                                showGallery={this.props.showGallery}
                                showingGallery={this.props.showingGallery}
                                onSelectInfoReturn={this.props.onSelectInfoReturn}
                                selectedBook={this.state.book}
                                addQuote={this.props.addQuote}
                                remQuote={this.props.remQuote}
                                editQuote={this.props.editQuote}
                                remBook={this.whenDeleting}
                                editBook={this.props.editBook}
                                isMobile={this.props.isMobile}
                                minimalDescription={this.props.minimalDescription}
                                changeHideHandler={this.changeHideHandler}
                                changeMenuHandler={this.changeMenuHandler}
                            />
                        </div>
                    </div>
                    <Modal id="barModal" isOpen={this.state.modalBar} onRequestClose={this.changeModalStateBar} style={{
                        overlay: {
                            color: "lightgrey"
                        },
                        content: {
                            color: 'black',
                            backgroundColor: "transparent",
                            border: "0px",
                            height: "100%",
                            top: "0",
                            left: "0"
                        }}}
                    >
                        <div id='menuBarMobile' className='ui segment'>
                            <div id='barIcons'>
                                <div className='ui icon button big' data-content="Add a book" onClick={this.returnForAdd}>
                                    <i className="plus icon large"></i>
                                </div>
                                <div className='ui icon button big' onClick={this.props.downloadTxt}>
                                    <i className="download icon large"></i>
                                </div>
                                <div className='ui icon button big' onClick={this.returnForUpload}>
                                    <i className="upload icon large"></i>
                                </div>
                                <div className='ui icon button big' onClick={this.props.saveOnStorage}>
                                    <i className="save icon large"></i>
                                </div>
                            </div>
                            <div id='barBooks' className='ui segment'>
                                <BookList books={this.props.books} selectBook={this.returnOnClick} />
                            </div>
                        </div>
                    </Modal>
                </div>
                
            )
    }
    
}



export default Menu;